import $ from 'jquery';
import 'slick-carousel';

$('.slick-slider').slick({
  prevArrow: '<span class="slider-arrow--left"></span>',
  nextArrow: '<span class="slider-arrow--right"></span>',

  dots: true,
  responsive: [
    {
      breakpoint: 767,
      settings: {
        dots: false
      }
    }
  ]

});
