$('.product-slider-for').slick({
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    fade: true,
    asNavFor: '.slider-nav',
    responsive: [
        {
            breakpoint: 992,
            settings: {
                dots: true,
            }
        }
    ]
});

$('.slider-nav').slick({
    slidesToShow: 3,
    slidesToScroll: 1,
    asNavFor: '.product-slider-for',
    focusOnSelect: true,
    centerMode: true,
    centerPadding: '0px',
    arrows: false
});
