const menuBurgerOpen = document.querySelector('[data-menuBurgerOpen]');
const menuBurger = document.querySelector('[data-menuBurger]');

menuBurgerOpen.addEventListener('click', () => {
    if (!(menuBurger.getAttribute('style'))) {
        menuBurger.setAttribute('style', 'display: flex');
    } else {
        menuBurger.removeAttribute('style');

    }
})