import $ from 'jquery';
import 'slick-carousel';

$('.slider').slick({
    prevArrow: '<span class="slider-arrow-catalog--left"></span>',
    nextArrow: '<span class="slider-arrow-catalog--right"></span>',
    infinite: true,

    slidesToShow: 4,
    slidesToScroll: 1,

    autoplay: 0,
    autoplaySpeed: 2000,
    arrows: true,
    pauseOnHover: true,
    responsive: [
        {
            breakpoint: 1200,
            settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
            }
        },
        {
            breakpoint: 900,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1
            }
        },
        {
            breakpoint: 610,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1
            }
        }
    ]
});
